* {
  font-family: 'Nunito', sans-serif; }

.App {
  text-align: center;
  display: block; }

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.home-container {
  margin: auto;
  display: inline-block; }

.App-link {
  color: #61dafb; }

.hm-text {
  color: black; }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

@media screen and (max-width: 800px) {
  .App {
    width: 100%; }
  .app-header {
    padding-top: 75vh; } }
