.proficiencies {
  background-color: #F8F8F8;
  height: 150px;
  overflow: hidden; }

.logo {
  margin-top: 25px;
  padding: 10px;
  height: 7vw; }

.sn, .npm {
  height: 5vw !important;
  padding-top: 30px; }

.outer {
  width: 100%;
  height: 150px;
  white-space: nowrap;
  position: absolute;
  overflow-x: scroll;
  overflow-y: hidden; }

.react-multiple-carousel__arrow {
  margin-top: 15px; }

::-webkit-scrollbar {
  display: none; }

.outer div {
  width: 24.5%;
  background-color: #eee;
  float: none;
  height: 90px;
  margin: 0 0.25%;
  display: inline-block; }
